<template>
    <div>
        <div id="dl">
            <p id="h">帐号密码登录</p>
            <hr >
            <template v-if="isfo">
                <img  id="tu3" :src=" '/api/img/'+ tx ">
            </template>
            <input type="text" name="name" id="in" placeholder="  请输入用户名" v-model="names" value="" /><br>
            <input type="password" name="pwd" id="in2" @click="dentx" placeholder="  请输入密码" v-model="pwd" value="" /><br>
            <input type="submit" @click="den" id="an" value="登录"  />
            <br><a href="#" @click.prevent="$router.push('/den2')" id="ai" >注册新账号</a>|
            <a href="#">意见反馈</a>

        </div>

        <el-dialog
                title=""
                :visible.sync="dialogVisible"
                width="30%">
            <span>登录成功！</span>
            <span slot="footer" class="dialog-footer">
        <!--            <el-button @click="dialogVisible = false">取 消</el-button>-->
                    <el-button type="primary" @click="$router.push('/kan')" >进入系统</el-button>
                </span>
        </el-dialog>

    </div>
</template>

<script>

    export default {
        name: "dzdiv",
        data() {
            return {
                names: '',
                pwd: '',
                tx: '',
                dialogVisible: false,
                isfo: false,
            }
        },
        methods: {
            den() {
                var rsp = new URLSearchParams();
                rsp.append("name", this.names);
                rsp.append("pwd", this.pwd);
                this.$axios({
                    url: "/api/gldl",
                    method: "post",
                    data: rsp,
                    withCredentials: true
                }).then(resp =>{
                    // console.log(resp);
                    // console.log(resp.data.data)
                    if (resp.data.data == 0){
                        // alert("用户名或密码错误！")
                        this.$message.error('用户名或密码错误！亲');
                    }else{
                        this.do()
                    }
                });
            },
            dentx() {
                var rsp = new URLSearchParams();
                rsp.append("name", this.names);
                this.$axios({
                    url: "/api/dltx",
                    method: "post",
                    data: rsp,
                }).then(resp =>{
                    console.log(resp);

                    if (resp.data.data == null){
                        // console.log('没有该用户');
                        this.isfo = false;
                        this.tx = null;
                        this.$message.error('没有该用户哦，亲');
                    }else{
                        // console.log('显示头像');
                        this.isfo = true;
                        this.tx = resp.data.data;
                    }
                });
            },
            do(){
                this.dialogVisible= true
            }
        },
        // created() {
        //     if (this.names!=null){
        //         console.log('111');
        //         this.dentx();
        //     }
        //     console.log('查询头像')
        // }
    }
</script>

<style scoped>
    #dl{
        background-color: #dfdfdf;
        opacity: 0.8;
        border: 1px solid gray;
        width: 330px;
        height: 330px;
        position: absolute;
        right: 300px;
        top: 120px;
        border-radius: 18px;

        transition: right 0.8s;
        -webkit-transition: right 0.8s;
    }
    #dl:hover{
        right: 320px;
    }
    #tu3{
        position: absolute;
        left: 30px;
        top: 20px;
        width: 60px;
        height: 60px;
        border-radius: 50px;
        border: 1px solid gray;
    }
    #h{
        margin-left: 115px;
    }
    a{
        color: #6f95c8;
        text-decoration:none;
    }
    #ai{
        margin-left: 140px;
    }
    a:hover{
        text-decoration: underline;
    }
    #in{
        border: 1px solid skyblue;
        border-radius: 12px;
        width: 260px;
        height: 40px;
        margin-top:30px;
        margin-left: 30px;
    }
    #in2{
        border: 1px solid skyblue;
        border-radius: 12px;
        width: 260px;
        height: 40px;
        margin-top:25px;
        margin-left: 30px;
    }
    #in:hover,#in2:hover{
        border: 1px solid #409AF8;
        border-radius: 12px;
    }
    #an{
        width: 265px;
        height: 45px;
        margin-top: 25px;
        margin-left: 30px;
        font-size: 20px;
        color: white;
        background-color: #3385ff;
        border: 0px;
        margin-bottom: 25px;
        border-radius: 10px;
    }
    #an:hover{
        opacity: 0.9;
    }
</style>