<template>
    <div>
        <hand></hand>


        <template v-if="isfo">
            <che2></che2>

            <form :model="form">
                <input type="text" id="name" placeholder="  作者" v-model="form.name" value="" /><br>
                <input type="text" id="vip" placeholder="  是否付费（是/否）" v-model="form.vip" value="" /><br>
<!--                <input type="text" id="yu" placeholder="  语种（国语/外语）" v-model="form.yu" value="" /><br>-->
                <input type="text" id="yu" placeholder="  类型" v-model="form.lx" value="" /><br>

                <img  id="tp"  title="请选择音乐图片..." @click="$refs.x.click()" :src="previewSrctp"/>
                <input style="display: none;"  type="file" ref="x" @change="filePicktp"/>

                <audio :src="previewSrcyy" controls="controls"  @click="$refs.yy.click()" id="yy"></audio>
                <input type="file" @change="filePickyy" id="xz" ref="yy" />

            </form>
            <button @click="savePost">点击上传</button>
        </template>

        <template v-else>
            <h1>请登录...</h1>
        </template>
    </div>
</template>

<script>
    import hand from '../components/hand';
    import che2 from '../components/che2';

    export default {
        name: "add2",
        components: {
            hand , che2
        },
        data() {
            return {
                isfo: false,
                form: {
                    name: '',
                    vip: '',
                    lx: '',
                    tp: null,
                    yy: null,
                },
                previewSrctp: null,
                previewSrcyy: null,
            }
        },
        methods: {
            doLogin() {
                this.$axios({
                    url: "/api/pd",
                    withCredentials: true
                }).then(resp =>{
                    console.log(resp);

                    if (resp.data.data.gname == null){
                        this.isfo = false
                    }else {
                        this.isfo = true
                    }
                });
            },
            savePost() {
                var formData = new FormData();
                formData.append("name", this.form.name);
                formData.append("vip", this.form.vip);
                formData.append("lx", this.form.lx);
                formData.append("tp", this.form.tp);
                formData.append("yy", this.form.yy);

                console.log(this.form.tp);
                console.log(this.form.yy);

                this.$axios({
                    url: '/api/addyy',
                    method: 'post',
                    data: formData
                }).then(r=> {
                    // alert("成功！");
                    this.$message({
                        message: '添加成功！',
                        type: 'success',
                        offset: '220'
                    });
                }).catch(e => {
                    alert(222)
                });
            },
            filePicktp() {
                this.previewSrctp = URL.createObjectURL(this.$refs.x.files[0]); //通过this.$refs.x 的方式来调用前面定义变量为x的节点元素
                this.form.tp = this.$refs.x.files[0];
            },
            filePickyy() {
                this.previewSrcyy = URL.createObjectURL(this.$refs.yy.files[0]); //通过this.$refs.x 的方式来调用前面定义变量为x的节点元素
                this.form.yy = this.$refs.yy.files[0];
            },

        },
        created() {
            this.doLogin();
        }
    }
</script>

<style scoped>
    #tp{
        position: absolute;
        top: 180px;
        left: 430px;
        width: 100px;
        height: 100px;
        border: 1px solid wheat;
        background-color: gray;
        color: white;
    }
    #yy{
        position: absolute;
        top: 210px;
        left: 550px;
        width: 300px;
        height: 30px;
    }
    #xz{
        position: absolute;
        top: 180px;
        left: 570px;
    }
    button{
        width: 100px;
        height: 35px;
        background-color: #409EFF;
        border: none;
        border-radius: 4px;
        color: white;
        position: absolute;
        left: 650px;
        top: 400px;
    }
    #name{
        position: absolute;
        top: 320px;
        left: 430px;
        width: 200px;
        height: 35px;
        border: 1px solid gray;
        border-radius: 5px;
    }
    #vip{
        position: absolute;
        top: 320px;
        left: 650px;
        width: 200px;
        height: 35px;
        border: 1px solid gray;
        border-radius: 5px;
    }
    #yu{
        position: absolute;
        top: 320px;
        left: 870px;
        width: 200px;
        height: 35px;
        border: 1px solid gray;
        border-radius: 5px;
    }
    #name:hover,#vip:hover,#yu:hover{
           border: 1px solid #409AF8;
       }
</style>