<template>
    <div>
        <hand></hand>


        <template v-if="isfo">
            <che></che>

            <div id="zhuti">
                <input type="text" id="in2"  placeholder="  请输入..." v-model="namelikes" value="" /><br>
                <input type="submit" @click="like" id="an" value="搜索"  />
                <input type="submit" @click="loadPosts" id="an2" value="返回全部"  />
<!--                <p>歌手总数：{{page.total}}</p>-->
                <div id="wr">
                    <span id="span">歌手总数</span>
                    <hr style="color: gray;">
                    <span id="shu">{{page.total}}</span>
                </div>

                    <el-pagination
                            background
                            layout="prev, pager, next"
                            @current-change="handlePageChange"
                            :current-page="page.current"
                            :page-size="page.size"
                            :total="page.total" id="tiao"
                            style="margin-bottom: 20px;margin-left: 50px">
                    </el-pagination><!--第几页、每页多少条、总条数-->

                <span id="tys">歌手</span>
                <span id="lx">介绍</span>

                <div v-for="p in gesou" :key="p.pid">

                    <div class="yyzhuti">
                        <img :src=" '/api/geshoutp/' +p.ptp " class="tp">
                        <div class="name">{{p.pname}}</div>
                        <div class="js">{{p.pjiesao}}</div>
<!--                        <button class="sc" @click="deleteComment(p.pid)">X</button>-->
                        <input type="submit" @click="deleteComment(p.pid)" id="ana" value="删除"  />
                    </div>

                </div>
            </div>

        </template>


        <template v-else>
            <h1>请登录...</h1>
        </template>
    </div>
</template>

<script>
    import hand from '../components/hand';
    import che from '../components/che';

    export default {
        name: "kan3",
        components: {
            hand , che
        },
        data() {
            return {
                isfo: false,
                page: {
                    current: 1,//第几页
                    size: 4,   //每页的数量
                    total: 0    //数据量
                },
                gesou: [],
                namelikes: ''
            }
        },
        methods: {
            doLogin() {
                this.$axios({
                    url: "/api/pd",
                }).then(resp =>{
                    console.log(resp);

                    if (resp.data.data.gname == null){
                        this.isfo = false
                    }else {
                        this.isfo = true
                    }
                });
            },
            loadPosts() {
                this.$axios({
                    url: "/api/kangs",
                    params: {
                        curr: this.page.current,
                        size: this.page.size
                    }
                }).then(r => {
                    console.log(r);
                    this.gesou = r.data.data;
                    this.page.total = r.data.page.total;
                }).catch(e => {
                    alert(e);
                });
            },
            handlePageChange(v) {
                this.page.current = v;
                this.loadPosts();
            },
            deleteComment(id) {
                this.$confirm("确定要删除?")
                    .then(() => {
                        this.$axios({
                            url: '/api/scgs?pid=' + id
                        }).then(r => {
                            this.$message("删除成功!");
                            this.loadPosts();
                        }).catch(e => {
                            this.$alert(e);
                        });
                    });
            },
            like() {
                var rsp = new URLSearchParams();
                rsp.append("name", this.namelikes);
                this.$axios({
                    url: "/api/szgs",
                    method: "post",
                    data: rsp,
                }).then(resp =>{
                    this.gesou = resp.data.data;
                });
            }

        },
        created() {
            this.doLogin();
            this.loadPosts();
        }
    }
</script>

<style scoped>
    #zhuti{
        position: absolute;
        /*border: 1px solid black;*/
        left: 230px;
        top: 90px;
        width: 1000px;
    }
    #tys{
        font-size: 18px;
        margin-left: 120px;
    }
    #lx{
        font-size: 18px;
        margin-left: 300px;
    }
    .yyzhuti{
        margin-top: 10px;
        position: relative;
        /* border: 1px solid black; */
        /* border-bottom: 1px solid #00AAFF; */
        /*background-color: #d4d4d4;*/
        background-color:  #E6E6FA;
        border-radius: 10px;
        box-shadow: 1px 1px 5px gray;
        margin-right: 30px;
        width: 1000px;
        height: 100px;
        overflow: hidden;
        float: left;
    }
    .tp{
        position: absolute;
        width: 70px;
        height: 70px;
        border-radius: 50px;
        left: 100px;
    }
    .name{
        position: absolute;
        left: 110px;
        top: 75px;
    }
    .js{
        position: absolute;
        width: 480px;
        height: 80px;
        left: 300px;
        top: 10px;
        /*border: 1px solid black;*/
        overflow: hidden;
    }
    .sc{
        position: absolute;
        right: 10px;
        top: 10px;
        width: 25px;
        height: 25px;
        color: red;
    }
    #ana{
        position: absolute;
        right: 70px;
        top: 25px;
        width: 40px;
        height: 25px;
        background-color: olivedrab;
        border: none;
        border-radius: 4px;
        color: white;
    }
    #wr{
        background-color: white;
        /* border: 1px solid red; */
        border-radius: 10px;
        box-shadow: 1px 1px 5px gray;
        width: 100px;
        height: 70px;
        box-shadow: gray;
        /*position: absolute;*/
        margin-top: 0px;
        margin-bottom: 10px;
        margin-left: 880px;
    }
    #shu{
        margin-left: 30px;
        font-size: 20px;
    }
    #in2{
        width: 260px;
        height: 35px;
        border: 1px solid gray;
        border-radius: 5px;
        position: absolute;
        left: 100px;
        top: 20px;
    }
    #in2:hover{
        border: 1px solid #409AF8;
    }
    #an{
        width: 60px;
        height: 35px;
        background-color: #409EFF;
        border: none;
        border-radius: 4px;
        color: white;
        position: absolute;
        left: 375px;
        top: 22px;
    }
    #an2 {
        width: 80px;
        height: 35px;
        background-color: #409EFF;
        border: none;
        border-radius: 4px;
        color: white;
        position: absolute;
        left: 460px;
        top: 22px;
    }
    #an:hover,#an2:hover{
        background-color: #3385ff;
        cursor: pointer;
    }
    #ana:hover{
        background-color: olive;
        cursor: pointer;
    }
</style>