<template>
    <div>
        <div id="dl">
            <p id="h">注册账户</p>
            <hr >
            <form :model="form">
                <img  id="tu3"  title="选择头像..." @click="$refs.x.click()" :src="previewSrc">
                <input style="display: none;" id="wj2" type="file" accept=".jpg, .gif" ref="x" @change="filePick">
                <input type="text" name="name" id="in" placeholder="  请输入用户名" v-model="form.name" value="" /><br>
                <input type="password" name="pwd" id="in2"  placeholder="  请输入密码" v-model="form.pwd" value="" /><br>
            </form>
            <input type="submit" @click="savePost" id="an" value="注册"  />
            <br><a href="#" id="ai" @click.prevent="$router.push('/den')">返回登陆</a>|
            <a href="#">意见反馈</a>

        </div>

        <el-dialog
                title=""
                :visible.sync="dialogVisible"
                width="30%">
            <span>注册成功！</span>
            <span slot="footer" class="dialog-footer">
        <!--            <el-button @click="dialogVisible = false">取 消</el-button>-->
                    <el-button type="primary" @click="$router.push('/den')" >立即登录</el-button>
                </span>
        </el-dialog>

    </div>
</template>

<script>

    export default {
        name: "dzdiv2",
        data() {
            return {
                form: {
                    name: '',
                    pwd: '',
                    cover: null,
                },
                previewSrc: null,
                dialogVisible: false
            }
        },
        methods: {
            savePost() {
                var formData = new FormData();
                formData.append("name", this.form.name);
                formData.append("pwd", this.form.pwd);
                formData.append("cover", this.form.cover);

                console.log(this.form.cover)

                this.$axios({
                    url: '/api/glzc',
                    method: 'post',
                    data: formData
                }).then(r=> {
                    // alert("注册成功！");
                    this.do()
                }).catch(e => {
                    alert(222)
                });
            },
            filePick() {
                this.previewSrc = URL.createObjectURL(this.$refs.x.files[0]); //通过this.$refs.x 的方式来调用前面定义变量为x的节点元素
                this.form.cover = this.$refs.x.files[0];
            },
            do(){
                this.dialogVisible= true
            }
        },
        // created() {
        //     this.doLogin();
        // }
    }
</script>

<style scoped>
    #dl{
        background-color: #dfdfdf;
