import VueRouter from "vue-router";
import den from "./components/den";  //引入组件
import den2 from "./components/den2";
import add from "./compon1/add";
import add2 from "./compon1/add2";
import add3 from "./compon1/add3";
import kan from "./compon1/kan";
import kan2 from "./compon1/kan2";
import kan3 from "./compon1/kan3";
import kan4 from "./compon1/kan4";
import tonji from "./compon1/tonji";
import tonji2 from "./compon1/tonji2";
import fankui from "./compon1/fankui";
import kan5 from "./compon1/kan5";


// 返回路由对象
export default new VueRouter({
    routes: [
        {
            path: '/', redirect: 'den'		//设置默认页面
        },
        {
            path: "/den", component: den
        },
        {
            path: "/den2", component: den2
        },
        {
            path: "/add", component: add
        },
        {
            path: "/add2", component: add2
        },
        {
            path: "/add3", component: add3
        },
        {
            path: "/kan", component: kan
        },
        {
            path: "/kan2", component: kan2
        },
        {
            path: "/kan3", component: kan3
        },
        {
            path: "/kan4", component: kan4
        },
        {
            path: "/tonji", component: tonji
        },
        {
            path: "/tonji2", component: tonji2
        },
        {
            path: "/fankui", component: fankui
        },
        {
            path: "/kan5", component: kan5
        },
    ]
})