<template>
    <div>
        <hand></hand>


        <template v-if="isfo">
            <che></che>


            <div id="zhuti">
                <input type="text" id="in2"  placeholder="  请输入..." v-model="namelikes" value="" /><br>
                <input type="submit" @click="like" id="an" value="搜索"  />
                <input type="submit" @click="loadPosts" id="an2" value="返回全部"  />
<!--                <p>音乐总数：{{page.total}}</p>-->
                <div id="wr">
                    <span id="span">音乐总数</span>
                    <hr style="color: gray;">
                    <span id="shu">{{page.total}}</span>
                </div>

                    <el-pagination
                            background
                            layout="prev, pager, next"
                            @current-change="handlePageChange"
                            :current-page="page.current"
                            :page-size="page.size"
                            :total="page.total" id="tiao"
                            style="margin-left: 50px;margin-bottom: 15px">
                    </el-pagination><!--第几页、每页多少条、总条数-->

                <span id="ttp">图片</span>
                <span id="tyy">歌曲名</span>
                <span id="tys">歌手</span>
                <span id="lx">付费</span>
                <span id="yz">类型</span>
                <span id="rd">获赞</span>

                <div v-for="yy in yinyue" :key="yy.mid">

                        <div class="yyzhuti">
                            <div class="yydiv">
                                <audio :src=" '/api/yinyue/' + yy.mname " controls="controls" class="yy"></audio>
                            </div>
                            <img :src=" '/api/yytp/' + yy.mtp " class="tp">
                            <div class="name">{{yy.mname.substring(0,yy.mname.indexOf('.'))}}</div>
                            <div class="zz">{{yy.mauthor}}</div>
<!--                            <div class="hui">{{yy.mvip}}</div>-->
<!--                            <div class="yu">{{yy.mguo}}</div>-->
                            <span class="hui" v-if="yy.mvip!=1">免费</span>
                            <span class="hui2" v-else>会员</span>
                            <span class="yu" >{{yy.mlx}}</span>
<!--                            <span class="yu" v-if="yy.mguo!=1">国语</span>-->
<!--                            <span class="yu" v-else>外语</span>-->
                            <div class="zan">{{yy.mlike}}</div>
                            <!--<a href="" class="sc" @click.prevent="deleteComment(yy.mid)">删除</a>-->
                            <input type="submit" @click="deleteComment(yy.mid)" id="ana" value="删除"  />
                        </div>

                </div>

            </div>
        </template>


        <template v-else>
            <h1>请登录...</h1>
        </template>
    </div>
</template>

<script>
    import hand from '../components/hand';
    import che from '../components/che';

    export default {
        name: "kan2",
        components: {
            hand , che
        },
        data() {
            return {
                isfo: false,
                page: {
                    current: 1,//第几页
                    size: 6,   //每页的数量
                    total: 0    //数据量
                },
                yinyue: [],
                namelikes: ''
            }
        },
        methods: {
            doLogin() {
                this.$axios({
                    url: "/api/pd",
                    withCredentials: true
                }).then(resp =>{
                    console.log(resp);

                    if (resp.data.data.gname == null){
                        this.isfo = false
                    }else {
                        this.isfo = true
                    }
                });
            },
            loadPosts() {
                this.$axios({
                    url: "/api/kanyy",
                    params: {
                        curr: this.page.current,
                        size: this.page.size
                    }
                }).then(r => {
                    console.log(r);
                    this.yinyue = r.data.data;
                    this.page.total = r.data.page.total;
                }).catch(e => {
                    alert(e);
                });
            },
            handlePageChange(v) {
                this.page.current = v;
                this.loadPosts();
            },
            deleteComment(id) {
                this.$confirm("确定要删除?")
                    .then(() => {
                        this.$axios({
                            url: '/api/scyy?mid=' + id
                        }).then(r => {
                            this.$message("删除成功!");
                            this.loadPosts();
                        }).catch(e => {
                            this.$alert(e);
                        });
                    });
            },

            like() {
                var rsp = new URLSearchParams();
                rsp.append("name", this.namelikes);
                this.$axios({
                    url: "/api/szyy",
                    method: "post",
                    data: rsp,
                }).then(resp =>{
                    this.yinyue = resp.data.data;
                });
            }

        },
        created() {
            this.doLogin();
            this.loadPosts();
        }
    }
</script>

