<template>
    <div>
        <hand></hand>

            <template v-if="isfo">
                <che></che>

                <div id="zhuti">
                    <h3>音乐总数：{{tj.zon}}</h3>
                    <p id="guoyu">国语：{{tj.guoyu}}</p>
                    <p id="waiyu">外语：{{tj.waiyu}}</p>
                    <p id="yyu">粤语：{{tj.yyu}}</p>
                    <p id="liuxin">流行：{{tj.liuxin}}</p>
                    <p id="minyao">民谣：{{tj.minyao}}</p>
                    <p id="qin">轻音乐：{{tj.qin}}</p>
                    <p id="gufen">古风：{{tj.gufen}}</p>
                    <p id="san">伤感：{{tj.san}}</p>
                    <p id="dj">DJ：{{tj.dj}}</p>
                    <p id="anjin">安静：{{tj.anjin}}</p>
                    <el-progress type="circle" :percentage="tj.guoyu2.substring(2)" class="bai"></el-progress>
                    <el-progress type="circle" :percentage="tj.waiyu2.substring(2)" class="bai"></el-progress><br>
                    <el-progress type="circle" :percentage="tj.yyu2.substring(2)" class="bai"></el-progress>
                    <el-progress type="circle" :percentage="tj.liuxin2.substring(2)" class="bai"></el-progress>
                    <el-progress type="circle" :percentage="tj.minyao2.substring(2)" class="bai"></el-progress>
                    <el-progress type="circle" :percentage="tj.qin2.substring(2)" class="bai"></el-progress>
                    <el-progress type="circle" :percentage="tj.gufen2.substring(2)" class="bai"></el-progress>
                    <el-progress type="circle" :percentage="tj.san2.substring(2)" class="bai"></el-progress>
                    <el-progress type="circle" :percentage="tj.dj2.substring(2)" class="bai"></el-progress>
                    <el-progress type="circle" :percentage="tj.anjin2.substring(2)" class="bai"></el-progress>
                </div>
            </template>


            <template v-else>
                <h1>请登录...</h1>
            </template>

    </div>
</template>

<script>
    import hand from '../components/hand';
    import che from '../components/che';

    export default {
        name: "tonji",
        components: {
            hand , che
        },
        data() {
            return {
                isfo: false,
                tj: [],
            }
        },
        methods: {
            doLogin() {
                this.$axios({
                    url: "/api/pd",
                }).then(resp => {
                    console.log(resp);

                    if (resp.data.data.gname == null) {
                        this.isfo = false
                    } else {
                        this.isfo = true
                    }
                });
            },
            loadPosts() {
                this.$axios({
                    url: "/api/tonji",
                }).then(r => {
                    console.log(r);
                    this.tj = r.data.data;
                }).catch(e => {
                    alert(e);
                });
            },

        },
        created() {
            this.doLogin();
            this.loadPosts();
        }
    }
</script>

