<template>
    <div>
        <hand></hand>


        <template v-if="isfo">
            <che></che>


            <div id="zhuti">

                        <el-pagination
                                background
                                layout="prev, pager, next"
                                @current-change="handlePageChange"
                                :current-page="page.current"
                                :page-size="page.size"
                                :total="page.total" id="tiao"
                                style="margin-bottom: 20px">
                        </el-pagination><!--第几页、每页多少条、总条数-->

                <div id="wr">
                    <span id="span">反馈总数</span>
                    <hr style="color: gray;">
                    <span id="shu">{{page.total}}</span>
                </div>


                <el-dialog title="回复用户" :visible.sync="dialogFormVisible">
                    <el-form :model="form">
                        <!--                                            <el-form-item label="用户id">-->
<!--                        <el-input type="hidden" v-model="form.id" autocomplete="off"></el-input>-->
                        <!--                                            </el-form-item>-->
                        <el-form-item label="回复">
                            <el-input type="textarea" v-model="form.hf" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-form>
                    <div slot="footer" class="dialog-footer">
                        <el-button @click="dialogFormVisible = false">取 消</el-button>
                        <el-button type="primary" @click.prevent="huifu" >确 定</el-button>
                    </div>
                </el-dialog>


                <div id="kaishi">
                    <h4 id="h4">用户留言</h4>

                        <div v-for="fks in fankui" :key="fks.fkid">

                            <div class="liuyin">
                                <img :src=" '/api/img2/'+fks.tp " class="tp">
                                <div class="name">{{fks.name}}</div>
                                <div class="fk">{{fks.fk}}</div>
                                <div class="time">{{fks.time}}</div>
                                <a href="#" class="hf" @click.prevent="kai(fks.id,fks.fk)">回复</a>
                                <a href="#" class="sc" @click.prevent="deleteComment(fks.fk)">删除</a>

                                <p class="bi" v-if="fks.sf==1">已回复</p>
                                <p class="bi2" v-else>未回复信息</p>
                            </div>

                        </div>
                </div>

            </div>

        </template>


        <template v-else>
            <h1>请登录...</h1>
        </template>
    </div>
</template>

<script>
    import hand from '../components/hand';
    import che from '../components/che';

    export default {
        name: "fankui",
        components: {
            hand , che
        },
        data() {
            return {
                isfo: false,
                page: {
                    current: 1,//第几页
                    size: 5,   //每页的数量
                    total: 0    //数据量
                },
                fankui: [],
                dialogFormVisible: false,
                form: {
                    id: '',
                    hf: '',
                    fk: ''
                },
            }
        },
        methods: {
            doLogin() {
                this.$axios({
                    url: "/api/pd",
                }).then(resp =>{
                    console.log(resp);

                    if (resp.data.data.gname == null){
                        this.isfo = false
                    }else {
                        this.isfo = true
                    }
                });
            },
            loadPosts() {
                this.$axios({
                    url: "/api/kanfk",
                    params: {
                        curr: this.page.current,
                        size: this.page.size
                    }
                }).then(r => {
                    console.log(r);
                    this.fankui = r.data.data;
                    this.page.total = r.data.page.total;
                }).catch(e => {
                    alert(e);
                });
            },
            handlePageChange(v) {
                this.page.current = v;
                this.loadPosts();
            },
            deleteComment(fk) {
                this.$confirm("确定要删除?")
                    .then(() => {
                        this.$axios({
                            url: '/api/scfk?fk=' + fk
                        }).then(r => {
                            this.$message("删除成功!");
                            this.loadPosts();
                        }).catch(e => {
                            this.$alert(e);
                        });
                    });
            },
            huifu() {
                var formData = new FormData();
                formData.append("id", this.form.id);
                formData.append("hf", this.form.hf);

                console.log(this.form.id)

                this.$axios({
                    url: '/api/hf',
                    method: 'post',
                    data: formData
                }).then(r=> {
                    // alert("注册成功！");
                    this.do();
                    this.bian(this.form.fk);
                    this.$message({
                        message: '已回复',
                        type: 'success'
                    });
                }).catch(e => {
                    alert(222)
                });
            },
            bian(fk){
                this.$axios({
                    url: '/api/fhlbian?fk=' +fk,
                }).then(r=> {
                    // alert("注册成功！");
                    console.log(111);
                    this.loadPosts();
                }).catch(e => {
                    alert(222)
                });
            },
            kai(id,fk){
                this.dialogFormVisible = true;
                this.form.id = id;
                this.form.fk = fk;
            },
            do(){
                this.dialogFormVisible = false;
            }

        },
        created() {
            this.doLogin();
            this.loadPosts();
        }
    }
</script>

<style scoped>
    #zhuti{
        position: absolute;
        /* border: 1px solid black; */
        left: 280px;
        top: 100px;
        width: 1000px;
    }
    #kaishi{
        width: 800px;
        border: 1px solid darkgray;
        border-radius: 10px;
        background-color: white;
        box-shadow: 1px 1px 10px gray;
    }
    #h4{
        margin-left: 20px;
    }
    .liuyin{
        position: relative;
        margin-left: 20px;
        margin-top: 20px;
        /*border: 1px solid black;*/
        height: 130px;
        width: 700px;

        background-color: #E6E6FA;
        border-radius: 10px;
        box-shadow: 1px 1px 5px gray;
    }
    .tp{
        margin-top: 10px;
        margin-left: 10px;
        width: 40px;
        height: 40px;
        border-radius: 50px;
    }
    .name{
        position: absolute;
        top: 20px;
        left: 60px;
    }
    .fk{
        position: absolute;
        top: 50px;
        left: 60px;
        overflow: hidden;
        width: 400px;
        /* border: 1px solid black; */
    }
    .time{
        position: absolute;
        top: 100px;
        left: 60px;
    }
    .hf{
        position: absolute;
        top: 100px;
        right: 180px;
    }
    .sc{
        position: absolute;
        top: 100px;
        right: 120px;
    }
    .bi{
        color: #007DDB;
        position: absolute;
        top: 5px;
        right: 30px;
    }
    .bi2{
        color: red;
        position: absolute;
        top: 5px;
        right: 30px;
    }
    #wr{
        background-color: white;
        /* border: 1px solid red; */
        border-radius: 10px;
        box-shadow: 1px 1px 5px gray;
        width: 100px;
        height: 70px;
        box-shadow: gray;
        position: absolute;
        /*margin-top: 0px;*/
        /*margin-bottom: 10px;*/
        /*margin-left: 880px;*/
        top: 20px;
        right: 60px;
    }
    #shu{
        margin-left: 30px;
        font-size: 20px;
    }
</style>