<template>
    <div class="zhuti">

        <img :src=" '/api/tp/logos.jpg' " class="logo">

        <ul>
            <li  @click="$router.push('/tonji')">音乐统计</li>
            <li  @click="$router.push('/tonji2')">热度统计</li>
            <li  @click="$router.push('/fankui')">用户反馈</li>
        </ul>

        <template v-if="isfo">
            <p class="y">当前登录：</p>
            <p class="yhname">{{yh.gname}}</p>
            <p class="zx" @click="zhuxiao">退出</p>
            <img :src=" '/api/img/'+ yh.gtp " class="yhtx">
        </template>

        <template v-else>
            <p class="y2"  @click="$router.push('/den')">请登录..</p>
        </template>

                <el-dialog
                        title=""
                        :visible.sync="dialogVisible"
                        width="30%">
                    <span>已注销！</span>
                    <span slot="footer" class="dialog-footer">
            <!--            <el-button @click="dialogVisible = false">取 消</el-button>-->
                        <el-button type="primary" @click="$router.push('/den')" >确定</el-button>
                    </span>
                </el-dialog>
    </div>
</template>

<script>

    export default {
        name: "hand",
        data() {
            return {
                yh: {},
                isfo: false,
                dialogVisible: false
            }
        },
        methods: {
            doLogin() {
                this.$axios({
                    url: "/api/pd",
                }).then(resp =>{
                    console.log(resp);
                    console.log(resp.data.data);

                    this.yh = resp.data.data;
                    this.isfo = true
                });
            },
            zhuxiao() {
                this.$axios({
                    url: "/api/glzx",
                }).then(resp =>{
                    this.dialogVisible= true
                });
            }

        },
        created() {
            this.doLogin();
        }
    }
</script>

<style scoped>
    body{
        margin: 0px;
        padding: 0px;
    }
    .zhuti{
        /*position: fixed;*/
        /*top: 10px;*/
        /*left: 0px;*/
        width: 100%;
        height: 70px;
        margin-top: 0px;
        margin-left: 0px;
        background-color: #23262E;
    }
    .logo{
        position: absolute;
        left: 50px;
        top: 20px;
        width: 90px;
        height: 60px;
    }
    ul{
        list-style-type: none;
        margin-left: 220px;
    }
    ul li{
        float: left;
        margin-right: 45px;
        font-size: 16px;
        color: white;
        margin-top: 25px;
    }
    ul li:hover{
        color: #01AAED;
        cursor: pointer;
    }
    .y,.y2{
        position: absolute;
        right: 220px;
        top: 25px;
        color: white;
        font-size: 16px;
    }
    .yhname{
        position: absolute;
        right: 110px;
        top: 25px;
        color: white;
        font-size: 16px;
    }
    .yhtx{
        position: absolute;
        right: 180px;
        top: 30px;
        width: 40px;
        height: 40px;
        border-radius: 30px;
        border: 1px solid green;
    }
    .zx{
        position: absolute;
        right: 30px;
        top: 25px;
        color: goldenrod;
        font-size: 16px;
    }
    .zx:hover{
        cursor: pointer;
    }
</style>