<script>
    import hand from '../components/hand';
    import che from '../components/che';

    export default {
        name: "tonji2",
        components: {
            hand , che
        },
        data() {
            return {
                isfo: false,
                tj: [],
            }
        },
        methods: {
            doLogin() {
                this.$axios({
                    url: "/api/pd",
                }).then(resp => {
                    console.log(resp);

                    if (resp.data.data.gname == null) {
                        this.isfo = false
                    } else {
                        this.isfo = true
                    }
                });
            },
            loadPosts() {
                this.$axios({
                    url: "/api/tonji2",
                }).then(r => {
                    console.log(r);
                    this.tj = r.data.data;
                }).catch(e => {
                    alert(e);
                });
            },

        },
        created() {
            this.doLogin();
            this.loadPosts();
        }
    }
</script>

