<template>
    <div>
        <hand></hand>


        <template v-if="isfo">
            <che3></che3>

            <div id="zhuti">
                <div id="lxzi">
                    <span class="djto">敏感字段库：</span>
                    <span class="djlxzi">钱、</span>
                    <span class="djlxzi">死、</span>
                    <span class="djlxzi">侵权、</span>
                    <span class="djlxzi">傻、</span>
                    <span class="djlxzi">滚、</span>
                    <span class="djlxzi">他妈、</span>
                    <span class="djlxzi">奸、</span>
                    <span class="djlxzi">贱、</span>
                    <span class="djlxzi">干、</span>
                    <span class="djlxzi">草、</span>
                    <span class="djlxzi">智障、</span>
                </div>

                <input type="text" id="in2"  placeholder="  请输入..." v-model="namelikes" value="" /><br>
                <input type="submit" @click="like" id="an" value="搜索"  />
                <input type="submit" @click="loadPosts" id="an2" value="返回全部"  />


                <span id="yhid">用户昵称</span>
                <span id="tys">评论内容</span>
                <span id="sj">注册时间</span>

                <div v-for="pl in yonhu" :key="pl.mcid">

                    <diyhwgplv class="yyzhuti">
                        <img :src=" '/api/img2/'+pl.yhtx " class="tp">
                        <div class="name">{{pl.yhname}}</div>
                        <div class="nl">{{pl.mComment}}</div>
                        <div class="time">{{pl.mtime}}</div>
                        <input type="submit" @click="jingao(pl.yhid,pl.mComment,pl.mtime)" id="anjg" value="发送警告"  />
                        <input type="submit" @click="deleteComment(pl.mcid)" id="ana" value="销毁评论"  />
                    </diyhwgplv>

                </div>
            </div>

        </template>


        <template v-else>
            <h1>请登录...</h1>
        </template>
    </div>
</template>

<script>
    import hand from '../components/hand';
    import che3 from '../components/che3';

    export default {
        name: "kan5",
        components: {
            hand , che3
        },
        data() {
            return {
                isfo: false,
                yonhu: [],
                namelikes: '',
            }
        },
        methods: {
            doLogin() {
                this.$axios({
                    url: "/api/pd",
                }).then(resp =>{
                    console.log(resp);

                    if (resp.data.data.gname == null){
                        this.isfo = false
                    }else {
                        this.isfo = true
                    }
                });
            },
            loadPosts() {
                this.$axios({
                    url: "/api/yhwgpl",
                }).then(r => {
                    console.log(r);
                    this.yonhu = r.data.data;
                }).catch(e => {
                    alert(e);
                });
            },
            deleteComment(id) {
                this.$confirm("确定要删除?")
                    .then(() => {
                        this.$axios({
                            url: '/api/scplwg?id=' + id
                        }).then(r => {
                            this.$message("删除成功!");
                            this.loadPosts();
                        }).catch(e => {
                            this.$alert(e);
                        });
                    });
            },
            like() {
                var rsp = new URLSearchParams();
                rsp.append("name", this.namelikes);
                this.$axios({
                    url: "/api/szwgpl",
                    method: "post",
                    data: rsp,
                }).then(resp =>{
                    this.yonhu = resp.data.data;
                });
            },
            jingao(id,pl,time) {
                var formData = new FormData();
                formData.append("id", id);
                formData.append("pl", pl);
                formData.append("time", time);

                this.$axios({
                    url: '/api/fasonjiangao',
                    method: 'post',
                    data: formData
                }).then(r=> {
                    this.$message({
                        message: '已警告',
                        type: 'success'
                    });
                }).catch(e => {
                    alert(222)
                });
            },

        },
        created() {
            this.doLogin();
            this.loadPosts();
        }
    }
</script>

<style scoped>
    #lxzi{
        padding-left: 20px;
