        border-bottom: 1px solid green;
    }
    .djto{
        margin-right: 50px;
        font-weight: 600;
    }
    .djlxzi{
        margin-right: 5px;
        margin-top: 0px;
    }
    /*.djlxzi:hover{*/
    /*    cursor: pointer;*/
    /*    background-color: red;*/
    /*    color: white;*/
    /*}*/

    #zhuti{
        position: absolute;
        /*border: 1px solid black;*/
        left: 260px;
        top: 120px;
        width: 1000px;
        padding-top: 80px;
    }
    #yhid{
        font-size: 18px;
        margin-left: 40px;
    }
    #tys{
        font-size: 18px;
        margin-left: 140px;
    }
    #sj{
        font-size: 18px;
        margin-left: 270px;
    }
    .yyzhuti{
        margin-top: 10px;
        position: relative;
        /* border: 1px solid black; */
        /* border-bottom: 1px solid #00AAFF; */
        /*background-color: #d4d4d4;*/
        background-color:  #E6E6FA;
        border-radius: 10px;
        box-shadow: 1px 1px 5px gray;
        margin-right: 30px;
        width: 1000px;
        height: 70px;
        overflow: hidden;
