        border-radius: 50px;
        left: 10px;
        margin-top: 10px;
    }
    .name{
        position: absolute;
        left: 70px;
        top: 20px;
    }
    .nl{
        width: 300px;
        height: 40px;
        /* border: 1px solid red; */
        position: absolute;
        left: 240px;
        top: 20px;
        overflow: hidden;
    }

    .time{
        position: absolute;
        left: 600px;
        top: 20px;
        /* border: 1px solid black; */
        overflow: hidden;
    }

    #ana{
        position: absolute;
        right: 70px;
        top: 25px;
