<template>
    <div>
        <hand></hand>


        <template v-if="isfo">
            <che2></che2>

            <form :model="form">
                <input type="text" id="name" placeholder="  作者" v-model="form.name" value="" /><br>
                <input type="text" id="vip" placeholder="  是否付费（是/否）" v-model="form.vip" value="" /><br>

                <video :src="previewSrc" @click="$refs.x.click()" controls="controls" id="sp"></video>
                <input type="file" ref="x" @change="filePick" id="xz" />
            </form>
                <button @click="savePost">点击上传</button>
        </template>

        <template v-else>
            <h1>请登录...</h1>
        </template>
    </div>
</template>

<script>
    import hand from '../components/hand';
    import che2 from '../components/che2';

    export default {
        name: "add",
        components: {
            hand , che2
        },
        data() {
            return {
                isfo: false,
                form: {
                    name: '',
                    vip: '',
                    cover: null,
                },
                previewSrc: null,
            }
        },
        methods: {
            doLogin() {
                this.$axios({
                    url: "/api/pd",
                    withCredentials: true
                }).then(resp =>{
                    console.log(resp);

                    if (resp.data.data.gname == null){
                        this.isfo = false
                    }else {
                        this.isfo = true
                    }
                });
            },
            savePost() {
                var formData = new FormData();
                formData.append("name", this.form.name);
                formData.append("vip", this.form.vip);
                formData.append("cover", this.form.cover);

                console.log(this.form.cover)

                this.$axios({
                    url: '/api/addsp',
                    method: 'post',
                    data: formData
                }).then(r=> {
                    // alert("成功！");
                    this.$message({
                        message: '添加成功！',
                        type: 'success',
                        offset: '240'
                    });
                }).catch(e => {
                    alert(222)
                });
            },
            filePick() {
                this.previewSrc = URL.createObjectURL(this.$refs.x.files[0]); //通过this.$refs.x 的方式来调用前面定义变量为x的节点元素
                this.form.cover = this.$refs.x.files[0];
            },

        },
        created() {
            this.doLogin();
        }
    }
</script>

<style scoped>
    #sp{
        position: absolute;
        top: 120px;
        left: 280px;
        width: 700px;
        height: 400px;
        border: 1px solid wheat;
        background-color: #01AAED;
    }
    #xz{
        position: absolute;
        top: 140px;
        right: 70px;
    }
    button{
        position: absolute;
        top: 320px;
        right: 120px;
        width: 80px;
        height: 35px;
        background-color: #409EFF;
        border: none;
        border-radius: 4px;
        color: white;
    }
    #name{
        position: absolute;
        top: 200px;
        right: 120px;
        width: 200px;
        height: 35px;
        border: 1px solid gray;
        border-radius: 5px;
    }
    #name:hover,#vip:hover{
        border: 1px solid #409AF8;
    }
    #vip{
        position: absolute;
        top: 260px;
        right: 120px;
        width: 200px;
        height: 35px;
        border: 1px solid gray;
        border-radius: 5px;
    }
</style>