<template>
    <div>
        <hand></hand>


        <template v-if="isfo">
            <che></che>

            <div id="zhuti">
                <input type="text" id="in2"  placeholder="  请输入..." v-model="namelikes" value="" /><br>
                <input type="submit" @click="like" id="an" value="搜索"  />
                <input type="submit" @click="loadPosts" id="an2" value="返回全部"  />
<!--                <p>视频总数：{{page.total}}</p>-->
                <div id="wr">
                    <span id="span">视频总数</span>
                    <hr style="color: gray;">
                    <span id="shu">{{page.total}}</span>
                </div>

                    <el-pagination
                            background
                            layout="prev, pager, next"
                            @current-change="handlePageChange"
                            :current-page="page.current"
                            :page-size="page.size"
                            :total="page.total" id="tiao"
                            style="margin-left: 50px">
                    </el-pagination><!--第几页、每页多少条、总条数-->


                    <div v-for="sp in shipin" :key="sp.vid">
                            <div class="spzhuti">
                                <video :src=" '/api/shipin/' + sp.vname " controls="controls" class="sp"></video>
                                <div class="name">{{sp.vname.substring(0,sp.vname.indexOf('.'))}}</div>
                                <span class="zz">{{sp.vauthor}}</span>
                                <br>
                                <span class="z">点赞：{{sp.vlike}}</span>
                                <span class="hui" v-if="sp.vvip!=1">免费视频</span>
                                <span class="hui2" v-else>会员专享</span>
                                <button class="sc" @click="deleteComment(sp.vid)">X</button>
                            </div>
                    </div>
            </div>
        </template>


        <template v-else>
            <h1>请登录...</h1>
        </template>
    </div>
</template>

<script>
    import hand from '../components/hand';
    import che from '../components/che';

    export default {
        name: "kan",
        components: {
            hand , che
        },
        data() {
            return {
                isfo: false,
                page: {
                    current: 1,//第几页
                    size: 8,   //每页的数量
                    total: 0    //数据量
                },
                shipin: [],
                namelikes: ''
            }
        },
        methods: {
            doLogin() {
                this.$axios({
                    url: "/api/pd",
                }).then(resp =>{
                    console.log(resp);

                    if (resp.data.data.gname == null){
                        this.isfo = false
                    }else {
                        this.isfo = true
                    }
                });
            },
            loadPosts() {
                this.$axios({
                    url: "/api/kansp",
                    params: {
                        curr: this.page.current,
                        size: this.page.size
                    }
                }).then(r => {
                    console.log(r);
                    this.shipin = r.data.data;
                    this.page.total = r.data.page.total;
                }).catch(e => {
                    alert(e);
                });
            },
            handlePageChange(v) {
                this.page.current = v;
                this.loadPosts();
            },
            deleteComment(id) {
                this.$confirm("确定要删除?")
                    .then(() => {
                        this.$axios({
                            url: '/api/scsp?vid=' + id
                        }).then(r => {
                            this.$message("删除成功!");
                            this.loadPosts();
                        }).catch(e => {
                            this.$alert(e);
                        });
                    });
            },
            like() {
                var rsp = new URLSearchParams();
                rsp.append("name", this.namelikes);
                this.$axios({
                    url: "/api/szsp",
                    method: "post",
                    data: rsp,
                }).then(resp =>{
                    this.shipin = resp.data.data;
                });
            }

        },
        created() {
            this.doLogin();
            this.loadPosts();
        }
    }
</script>

