<template>
    <div>
        <hand></hand>


        <template v-if="isfo">
            <che2></che2>

            <form :model="form">
                <input type="text" id="name" placeholder="歌手名称:" v-model="form.name" value="" /><br>
                <textarea rows="10" cols="50" placeholder="歌手介绍..." id="jiesao" v-model="form.js"></textarea>

                <img  id="tp"  title="请选择歌手照片..." @click="$refs.x.click()" :src="previewSrc">
                <input style="display: none;"  type="file" ref="x" @change="filePick">
            </form>
            <button @click="savePost">点击上传</button>

        </template>

        <template v-else>
            <h1>请登录...</h1>
        </template>
    </div>
</template>

<script>
    import hand from '../components/hand';
    import che2 from '../components/che2';

    export default {
        name: "add3",
        components: {
            hand , che2
        },
        data() {
            return {
                isfo: false,
                form: {
                    name: '',
                    js: '',
                    tp: null,
                },
                previewSrc: null,
            }
        },
        methods: {
            doLogin() {
                this.$axios({
                    url: "/api/pd",
                    withCredentials: true
                }).then(resp =>{
                    console.log(resp);

                    if (resp.data.data.gname == null){
                        this.isfo = false
                    }else {
                        this.isfo = true
                    }
                });
            },
            savePost() {
                var formData = new FormData();
                formData.append("name", this.form.name);
                formData.append("js", this.form.js);
                formData.append("tp", this.form.tp);

                console.log(this.form.tp);

                this.$axios({
                    url: '/api/addgs',
                    method: 'post',
                    data: formData
                }).then(r=> {
                    // alert("成功！");
                    this.$message({
                        message: '添加成功！',
                        type: 'success',
                        offset: '220'
                    });
                    this.previewSrc = null;
                    this.form.name = null;
                    this.form.js = null;
                }).catch(e => {
                    alert(222)
                });
            },
            filePick() {
                this.previewSrc = URL.createObjectURL(this.$refs.x.files[0]); //通过this.$refs.x 的方式来调用前面定义变量为x的节点元素
                this.form.tp = this.$refs.x.files[0];
            },

        },
        created() {
            this.doLogin();
        }
    }
</script>

<style scoped>
    #tp{
        position: absolute;
        top: 180px;
        left: 430px;
        width: 100px;
        height: 100px;
        border: 1px solid wheat;
        background-color: gray;
        color: white;
    }
    button{
        width: 100px;
        height: 35px;
        background-color: #409EFF;
        border: none;
        border-radius: 4px;
        color: white;
        position: absolute;
        left: 650px;
        top: 450px;
    }
    #name{
        position: absolute;
        top: 200px;
        left: 590px;
        width: 200px;
        height: 35px;
        border: 1px solid gray;
        border-radius: 5px;
    }
    #jiesao{
        position: absolute;
        top: 270px;
        left: 590px;
        border: 1px solid gray;
        border-radius: 5px;
    }
</style>